@import url(https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
:root {
  --Header: 'Work Sans';
  --Accent: 'Montserrat';
  --color1: #0D1821;
  --color2: #344966;
  --color3: #B4CDED;
  --color4: #F0F4EF;
  --color5: #BFCC94;
  --color6: #F093AF;
  --color4-hover: #a3a7a2;
  --base: white;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100%;
  max-height: 100%;
  background-color: #F0F4EF;
  background-color: var(--color4);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


/* General styling */
h1,h2,h3,h4,h5,h6,a {
  font-family: 'Work Sans';
  font-family: var(--Header);
}

p, code, span,label,input,textarea {
  font-family: 'Montserrat';
  font-family: var(--Accent);
}

::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: #344966;
  background: var(--color2);
}
::-webkit-scrollbar-thumb {
  background: #F0F4EF;
  background: var(--color4);
}
::-webkit-scrollbar-thumb:hover {
  background: #a3a7a2;
  background: var(--color4-hover);
}


.App {
  min-height: 100vh;
  max-height: 100vh;
  width: 100%;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}

.Content{
  min-height: 95vh;
  max-height: 95vh;
  overflow: scroll;
  overflow-x: hidden;
}

@media (max-width: 599px) {
  .App {
    flex-flow: column-reverse;
  }
  .Content {
    min-height: 92vh;
    max-height: 92vh;
  }
}
@media (min-width: 600px) {
  .App {
    flex-flow: column-reverse;
  }
}
@media (min-width: 900px) {
  .App {
    flex-flow: column;
  }
}
.Navigation {
    display: flex;
    flex-flow: row wrap;
    width: 100%;
    background-color: var(--color2);
    height: 10vh;
    max-height: 10vh;
}

.Navigation > .InnerContainer {
    margin: auto;
}

a {
    margin: 6px;
    padding: 2px;
    color: var(--color4);
    cursor: pointer;
}

.Navigation > .InnerContainer > .active {
    background-color: var(--color6);
    padding: 5px;
    border-radius: 5px;
    font-weight: bold;
    transition: ease-in-out 0.5s;
}

