.Contact { 
    display: flex;
    min-height: 100vh;
    margin:  30px auto;
    width: 100%;
    padding: 50px;
}

.Contact > .InnerContainer {
    display: flex;
    flex-flow: row-reverse;
    margin: auto;
    width: 90%;
}

.Contact > .InnerContainer > .ContactInfo {
    padding: 10px;
    margin: 0px auto;
    display: flex;
    flex-flow: column;
    width: 30%;
    justify-content: space-between;
}
.Contact > .InnerContainer > .ContactInfo > .Info {
    display: flex;
    flex-flow: column;
}
.ContactInfo > .Info > img {
    margin: auto;
    height: 48px;
    width: 48px;
}

.ContactForm {
    display: flex;
    flex-flow: column;
    margin: 15px auto;
    width: 100%;
}

.ContactForm > .Input {
    margin: 10px 0;
}

.ContactForm > h1 {
    color: var(--color6);
    margin: 10px 0;
}

.ContactForm > .Input > label > input {
    height: 30px;
    border: none;
    padding: 5px;
    border: black solid;
    border-radius: 5px;
    background-color: var(--base);
    width: 65%;
}

.ContactForm > .Input > label > .Message {
    padding: 2px;
    margin: 5px auto;
    border: black solid;
    border-radius: 5px;
    height: 100px;
    width: 65%;
    resize: none;
}

.ContactForm > button {
    height: 30px;
    width: 20%;
    border: none;
    border-radius: 5px;
    color: var(--color4);
    background-color: var(--color6);
    cursor: pointer;
}



